#DispatchDetails {
  padding: 0 30px;
}
#DispatchDetails p {
  margin: 0;
  padding: 0;
}
#DispatchDetails .detailsTitle {
  display: flex;
  justify-content: space-between;
  margin: 10px 0 0 0;
  align-items: center;
}
#DispatchDetails #detailsMsg {
  display: flex;
  justify-content: space-between;
}
#DispatchDetails #detailsMsg p {
  margin: 5px 0;
}
#DispatchDetails #orderDetails div:nth-of-type(1) p {
  margin: 8px 0;
}
#DispatchDetails #orderDetails div:nth-of-type(2) {
  margin: 15px 0;
}
#DispatchDetails #toPeiSon {
  display: flex;
  justify-content: center;
}
