#distribution {
  padding: 0 30px;
}
#distribution p {
  margin: 0;
  padding: 0;
}
#distribution .detailsTitle {
  display: flex;
  justify-content: space-between;
  margin: 10px 0 0 0;
  align-items: center;
}
/* #distribution #detailsMsg {
    display: flex;
    justify-content: space-between;
} */
#distribution #detailsMsg p {
  margin: 5px 0;
}
#distribution #orderDetails div:nth-of-type(1) p {
  margin: 8px 0;
}
#distribution #orderDetails div:nth-of-type(2) {
  margin: 15px 0;
}
#distribution .VoucherMsg {
  display: flex;
  justify-content: space-around;
}
#distribution .VoucherMsg div {
  text-align: center;
}
#distribution .VoucherMsg div p {
  margin: 5px 0;
  color: blue;
}
#distribution .rowBox {
  display: flex;
  justify-content: space-around;
  margin-bottom: 2vh;
}
