#details {
  padding: 0 30px;
}
#details p {
  margin: 0;
  padding: 0;
}
#details .detailsTitle {
  display: flex;
  justify-content: space-between;
  margin: 10px 0 0 0;
  align-items: center;
}
#details #detailsMsg .rowBox {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2vh;
}
/* #details #detailsMsg p {
    margin: 5px 0;
} */
#details #orderDetails div:nth-of-type(1) p {
  margin: 8px 0;
}
#details #orderDetails div:nth-of-type(2) {
  margin: 15px 0;
}
#details .VoucherMsg {
  display: flex;
  justify-content: space-around;
}
#details .VoucherMsg div {
  text-align: center;
}
#details .VoucherMsg div p {
  margin: 5px 0;
  color: blue;
}
