#r_details {
  padding: 0 30px;
}
#r_details p {
  margin: 0;
  padding: 0;
}
#r_details .r_detailsTitle {
  display: flex;
  justify-content: space-between;
  margin: 10px 0 0 0;
  align-items: center;
}
#r_details #r_detailsMsg {
  display: flex;
  justify-content: space-between;
}
.r_address {
  margin-top: 10px;
}
#r_details #r_orderDetails div:nth-of-type(1) p {
  margin: 8px 0;
}
#r_details #r_orderDetails div:nth-of-type(2) {
  margin: 15px 0;
}
#r_details #r_toPeiSon {
  display: flex;
  justify-content: center;
}
#r_shoppingVoucher {
  display: flex;
  justify-content: space-around;
}
.chooseOrder > p {
  display: inline-block;
}
.chooseOrder > label {
  margin-left: 50px;
}
.chooseTable {
  display: flex;
  /*justify-content:space-around;*/
  flex-wrap: wrap;
}
.chooseTable > div {
  margin-right: 100px;
}
.F_liebie {
  margin-top: 15px;
}
#r_toPeisong {
  margin-top: 100px;
}
